import styled from "@emotion/styled";

import { theme } from "@hitachivantara/uikit-react-core";

import { SUB_HEADER_HEIGHT } from "../../components/layout/Main/styles";

interface StyledErrorPageProps {
  showHeaderSubMenu?: boolean;
  isCompactMode?: boolean;
  fullPage?: boolean;
}

interface StyledImageWrapperProps {
  backgroundImage: string;
}

/**
 * Calculates the height of the image wrapper based on the header sub menu and compact mode.
 */
const calcHeight = ({
  showHeaderSubMenu,
  isCompactMode,
  fullPage
}: StyledErrorPageProps) => {
  if (fullPage) {
    return `100vh`;
  }
  if (showHeaderSubMenu && !isCompactMode) {
    return `calc(100vh - (${theme.header.height} + ${SUB_HEADER_HEIGHT}px + ${theme.space.lg} + ${theme.space.lg}))`;
  }
  return `calc(100vh - (${theme.header.height} + ${theme.space.lg} + ${theme.space.lg}))`;
};

const StyledErrorPage = styled.div<StyledErrorPageProps>`
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: ${theme.space.lg};
  height: ${(props: StyledErrorPageProps) => calcHeight(props)};
`;

const StyledImageWrapper = styled.div<StyledImageWrapperProps>`
  flex: 1;
  display: flex;
  background-image: ${props => props.backgroundImage};
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  flex-direction: column;
  height: 100%;
`;

const StyledTitleWrapper = styled.div`
  text-align: center;
`;

export { StyledTitleWrapper, StyledErrorPage, StyledImageWrapper };
